import { Injectable } from '@angular/core';
//http
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs'
import { filter,map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class GetWeatherApiService {

  // home
  public addCityForHome:any[] =[
  ];
  public mateData:any[] = [
  ]
  //api
  // api
  public domain:string = 'https://devapi.heweather.net/v7/weather/now?key=27760125f4f1448faa7ec3cf16dbce45'
  // id
  public getId:number;
  // id api
  public CityIdMain = 'https://geoapi.heweather.net/v2/city/lookup?&key=27760125f4f1448faa7ec3cf16dbce45&location='
  // 
  public weaterData3:string = 'https://devapi.heweather.net/v7/weather/7d?key=27760125f4f1448faa7ec3cf16dbce45'

  // public city:String;
  public citys = []

  constructor(public http: HttpClient) {

  }
  //
  getWeatherData(location:number){
    return new Promise((resolve,reject) => {
      this.http.get(`${this.domain}&location=${location}`).subscribe(
        (res) => {resolve(res)}
      )
    })
  }

  getCityID(city:String){
    return new Promise((resolve,reject) => {
      this.http.get(this.CityIdMain + city).subscribe(
        (res) => {resolve(res)}
      )
    })
  }
  // 3
  getWeatherDataMate(location:number){
    return new Promise((resolve,reject) => {
      this.http.get(`${this.weaterData3}&location=${location}`).subscribe(
        (res) => {resolve(res)}
      )
    })
  }


}
